  transition: background 0.3s ease-out; }
  .button-secondary:hover {
    background: #1c9a7c;
    transition: background 0.3s ease-out; }

.button-primary {
  background: #279add;
  color: #fff !important;
  transition: background 0.3s ease-out; }
  .button-primary:hover {
    background: #1c7cb4;
    transition: background 0.3s ease-out; }

.button-icon {
  padding-left: 3.5em; }
